* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-box {
  width: 350px;
  margin-bottom: 16px;
  display: inline-block;
  position: relative;
}

.search-box__input {
  font: inherit;
  width: 100%;
  padding: 8px 25px 8px 8px;
}

.search-box__input:placeholder-shown ~ .search-box__clear-btn {
  display: none;
}

.search-box__clear-btn {
  height: 100%;
  width: 28px;
  color: #212121;
  background-color: unset;
  border-style: unset;
  cursor: pointer;
  opacity: .5;
  padding: 0;
  font-size: 22px;
  line-height: 0;
  transition: opacity .25s ease-out;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.search-box__clear-btn:hover {
  opacity: 1;
}

.country-list:not(:empty) {
  margin-bottom: 15px;
}

.country-list__item {
  align-items: center;
  gap: 10px;
  display: flex;
}

.country-list__name {
  cursor: pointer;
}

.country-list__name:hover {
  background-color: #dbdbdb;
}

.country-list__flag, .country-info__flag {
  border: 1px solid #afafaf;
}

.country-info__list :not(:last-child) {
  margin-bottom: 5px;
}

.country-info__title {
  align-items: center;
  gap: 10px;
  margin-bottom: 5px;
  display: flex;
}

